
.parametr__content {
  z-index: 5;
}

.parametr__field {
  margin-bottom: 4.5%;

  input {
    width: 100%;
    box-sizing: border-box;
  }
}

.parametr__select {
  position: relative;
  &--success {
    &::after {
      content: "";
      position: absolute;
      background-image: url("../img/check.svg");
      top: 0;
      left: 0;
      width: 18px;
      height: 14px;
    }
  }
}



.parametr__button {
  display: block;
  margin: 0;
  width: 100px;
  padding: 20px 0;
  position: relative;
  box-shadow: -3px 2px 4px rgba(0, 0, 0, 0.25);

  &::after {
    content: "";
    position: absolute;
    top: 13%;
    right: -22%;
    width: 47px;
    height: 48px;
    background: linear-gradient(180deg, #818387 0%, #696b6f 100%);
    border-left: 2px solid transparent;
    border-bottom: 2px solid transparent;
    border-radius: 4px;
    transform: rotate(45deg);
    z-index: -1;
  }

  &--active {
    &::after {
      background: linear-gradient(180deg, #4892ec 0%, #2f79d4 100%);
    }
  }

  &:hover,
  &:focus {
    border-right: none;
    padding-bottom: 19px;
    padding-top: 19px;

    &::after {
      outline: none;
      width: 45px;
      height: 46px;
      right: -23%;
      top: 13.2%;
      background: linear-gradient(0deg, #3e8ff2, #3e8ff2);
    }
  }

  &:active {
    &::after {
      outline: none;
      background: linear-gradient(0deg, #256fca, #256fca);
    }
  }
}

.parametr__checkbox {

  input {
    background: linear-gradient(360deg, #f3f3f3 0%, rgba(243, 243, 243, 0) 100%);
    border: 1px solid #d7d7d7;
  }
}
